import React, {Component} from "react";
import {NavLink} from "react-router-dom";

class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
            menuIsOpen: false
        }
    }

    handlerMenuClick() {
        this.setState({menuIsOpen: !this.state.menuIsOpen});
        console.log(this.state.menuIsOpen);
    }

    render() {
        return (
            <div>
            <div className={`overlay large-0" ${this.state.menuIsOpen ? "" : "hide"}`}>
                <div className="menu-btn-icon border right"
                     onClick={this.handlerMenuClick.bind(this)}/>
                <div className="small-navigation">
                    <NavLink className="small-nav-item" to="/news"></NavLink>
                    <NavLink className="small-nav-item" to="/about"> </NavLink>
                    <NavLink className="small-nav-item" to="/contacts"></NavLink>
                    <NavLink className="small-nav-item" to="/projects"></NavLink>
                    <NavLink className="small-nav-item" to="/sponsors"></NavLink>
                    <NavLink className="small-nav-item" to="/club"> </NavLink>
                </div>
            </div>
            <header>
                <NavLink to="/"
                         activeClassName="selected">
                    <div className="logo-white"/>
                </NavLink>
                <div className="login-btn-icon-white small-0 medium-0"/>
                <div className="menu-btn-icon-white large-0"
                     onClick={this.handlerMenuClick.bind(this)}/>
                <div className="donate-btn-white small-0 medium-0"> </div>
                <div className="navigation small-0 medium-0">
                    <NavLink className="nav-item" to="/news"></NavLink>
                    <NavLink className="nav-item" to="/about"> </NavLink>
                    <NavLink className="nav-item" to="/contacts"></NavLink>
                    <NavLink className="nav-item" to="/projects"></NavLink>
                    <NavLink className="nav-item" to="/sponsors"></NavLink>
                    <NavLink className="nav-item" to="/club"> </NavLink>
                </div>
            </header>
            </div>
        );
    }
}


export default Header;



